<template>
  <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen>
    <el-tabs v-model="activeName">
      <el-tab-pane label="基本信息" name="first">
        <techo-people ref="vBaseForm" :isAdd="isAdd" :dic="dic" :houseOptions="houseOptions"
        @onBasePeople="handleQueryBasePeople" :disCodeOption="disCodeOption"></techo-people>
      </el-tab-pane>
      <el-tab-pane label="附加信息" name="second">
        <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">
          <el-row :gutter="15">
            <el-col :span="6">
              <el-form-item label="人员类型" prop="personType">
                <el-select placeholder="请选择人员类型" clearable v-model="dialogForm.personType">
                  <el-option v-for="(item, index) in dic['人员类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="家庭情况" prop="familySituation">
                <el-select placeholder="请选择家庭情况" clearable v-model="dialogForm.familySituation">
                  <el-option v-for="(item, index) in dic['家庭情况']" :label="item.name" :value="item.code" :key="index"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="监护人公民身份号码" prop="guardianCardnumber" label-width="150px">
                <el-input v-model="dialogForm.guardianCardnumber" :maxlength="18" placeholder="请填写监护人公民身份号码"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="监护人姓名" prop="guardianName">
                <el-input v-model="dialogForm.guardianName" :maxlength="50" placeholder="请填写监护人姓名"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="15">
            <el-col :span="6">
              <el-form-item label="与监护人关系" prop="guardianRelationship">
                <el-select placeholder="请选择与监护人关系" clearable v-model="dialogForm.guardianRelationship">
                  <el-option v-for="(item, index) in dic['户主关系']" :label="item.name" :value="item.code" :key="index"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="监护人联系方式" prop="guardianContact">
                <el-input v-model="dialogForm.guardianContact" :maxlength="50" placeholder="请填写监护人联系方式"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="监护人居住详址" prop="guardianAddress" label-width="150px">
                <el-input v-model="dialogForm.guardianAddress" :maxlength="200" placeholder="请填写监护人居住详址"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="是否违法犯罪">
                <el-radio-group v-model="dialogForm.iscriminal">
                  <el-radio :label="0">否</el-radio>
                  <el-radio :label="1">是</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="15">
            <el-col :span="24">
              <el-form-item label="违法犯罪情况">
                <el-input v-model="dialogForm.criminalSituation" type="textarea" :rows="3" :maxlength="1024"
                  placeholder="请填写违法犯罪情况"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="15">
            <el-col :span="6">
              <el-form-item label="帮扶人姓名" prop="helpName">
                <el-input v-model="dialogForm.helpName" :maxlength="50" placeholder="请填写帮扶人姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="帮扶人联系方式" prop="helpContact">
                <el-input v-model="dialogForm.helpContact" :maxlength="50" placeholder="请填写监护人姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="帮扶手段" prop="helpMeans" label-width="150px">
                <el-select placeholder="请选择帮扶手段" clearable v-model="dialogForm.helpMeans">
                  <el-option v-for="(item, index) in dic['帮扶手段']" :label="item.name" :value="item.code" :key="index"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">

            </el-col>
          </el-row>
          <el-row :gutter="15">
            <el-col :span="24">
              <el-form-item label="帮扶情况">
                <el-input v-model="dialogForm.helpSituation" type="textarea" :rows="3" :maxlength="1024" placeholder="请填写帮扶情况"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-tab-pane>
    </el-tabs>
    <div slot="footer" class="dialog-footer">
      <el-button v-if="activeName == 'first'" type="primary" size="small" @click="activeName = 'second'">下一步</el-button>
      <el-button v-if="activeName == 'second'" type="warning" size="small" @click="resetForm">取消</el-button>
      <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'first'">上一步</el-button>
      <el-button v-if="activeName == 'second'" type="success" size="small" @click="submitForm">保存</el-button>
    </div>
  </el-dialog>
</template>
<script>
import service from "./services/important_youngsters_service.js";
export default {
  props: {
    disCodeOption: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      activeName: "first",
      dic: {
        性别: [],
        民族: [],
        婚姻状况: [],
        政治面貌: [],
        学历: [],
        宗教信仰: [],
        职业类别: [],
        行政区划: [],
        人员类型: [],
        家庭情况: [],
        户主关系: [],
        帮扶手段: []
      },
      houseOptions: [],
      dialogForm: {
        iscriminal: 0,
        districtCode: ""
      },
      teamRules: {
        personType: [
          {
            required: true,
            message: "请选择人员类型",
            trigger: "change"
          }
        ],
        familySituation: [
          {
            required: true,
            message: "请选择家庭情况",
            trigger: "change"
          }
        ],
        guardianCardnumber: [
          // {
          //   required: true,
          //   message: "请输入监护人公民身份号码",
          //   trigger: "blur"
          // },
          util.validate.isIDCard()
        ],
        guardianName: [
          {
            required: true,
            message: "请输入监护人姓名",
            trigger: "blur"
          }
        ],
        guardianRelationship: [
          {
            required: true,
            message: "请选择与监护人关系",
            trigger: "change"
          }
        ],
        guardianContact: [
          // {
          //   required: true,
          //   message: "请输入监护人联系方式",
          //   trigger: "blur"
          // },
          util.validate.tellAndPhone()
        ],
        // guardianAddress: [
        //   {
        //     required: true,
        //     message: "请输入监护人居住详址",
        //     trigger: "blur"
        //   }
        // ],
        helpName: [
          {
            required: true,
            message: "请输入帮扶人姓名",
            trigger: "blur"
          }
        ],
        helpContact: [
          // {
          //   required: true,
          //   message: "请输入帮扶人联系方式",
          //   trigger: "blur"
          // },
          util.validate.tellAndPhone()
        ],
        helpMeans: [
          {
            required: true,
            message: "请选择帮扶手段",
            trigger: "change"
          }
        ]
      }
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            if (item.name == "职业类别") {
              that.dic[item.name] = that.handleTreeData(data);
            } else {
              that.dic[item.name] = data;
            }
          });
        }
      });
    });
    /** 获取户信息 */
    service.requestHouse(data => {
      this.houseOptions = data.list;
    });
  },

  watch: {
    activeName(name) {
      if (name == "second") {
        this.$refs["vBaseForm"].$refs["baseForm"].validate(val => {
          if (!val) {
            this.$nextTick(() => {
              this.activeName = "first";
            });
          }
        });
      }
    }
  },

  methods: {
    // 职业类型组成树形数据
    handleTreeData(val) {
      // 大类
      let itemONE = _.map(
        _.filter(val, street => {
          return street.code.slice(1, 5) === "0000";
        }),
        street => {
          return {
            code: street.code,
            label: street.name,
            children: []
          };
        }
      );
      // 中类
      _.map(itemONE, item => {
        item.children = _.map(
          _.filter(val, street => {
            return (
              street.code.slice(0, 1) === item.code.slice(0, 1) &&
              street.code.slice(1, 5) !== "0000" &&
              street.code.slice(3, 5) === "00"
            );
          }),
          street => {
            return {
              code: street.code,
              label: street.name,
              children: []
            };
          }
        );
      });
      // 小类
      _.map(itemONE, midItem => {
        _.map(midItem.children, item => {
          item.children = _.map(
            _.filter(val, street => {
              return (
                street.code.slice(0, 3) === item.code.slice(0, 3) &&
                street.code.slice(3, 5) !== "00"
              );
            }),
            street => {
              return {
                code: street.code,
                label: street.name
              };
            }
          );
        });
      });
      return itemONE;
    },
    handleQueryBasePeople(idCardNo, callback) {
      service.requestBasePeopleList(idCardNo, data => {
        callback && callback(data.list);
      });
    },
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let baseInfo = _.cloneDeep(this.$refs["vBaseForm"].baseForm);
          baseInfo.birthDate = baseInfo.birthDate && util.time.formatDate(baseInfo.birthDate,"YYYY-MM-DD");
          baseInfo.occupationType = _.last(baseInfo.occupationType);
          baseInfo.dicMap && delete baseInfo.dicMap;
          if (baseInfo.id) {
            // 修改基本信息
            service.requestUpdatePeopleInfo(baseInfo, data => {
              this.comminForm(data);
            });
          } else {
            // 添加基本信息
            service.requestSavePeopleInfo(baseInfo, data => {
              this.comminForm(data);
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    comminForm(data) {
      let otherInfo = _.cloneDeep(this.dialogForm);
      otherInfo.dicMap && delete otherInfo.dicMap;
      if (this.isAdd == true) {
        otherInfo.peopleId = data.id;
        // 添加 重点青少年附加信息
        service.requestSaveImportantYoungsters(otherInfo, data => {
          this.$popup.showNotification({
            type: "success",
            message: "重点青少年添加成功！"
          });
          this.resetForm();
          this.$parent.handleQuery();
        });
      } else {
        // 修改
        let param = _.pick(otherInfo, [
          "name",
          "personType",
          "familySituation",
          "guardianCardnumber",
          "guardianName",
          "guardianRelationship",
          "guardianContact",
          "guardianAddress",
          "iscriminal",
          "criminalSituation",
          "helpName",
          "helpContact",
          "helpMeans",
          "helpSituation",
          "districtCode",
          "id",
          "peopleId"
        ]);
        service.requestUpdateImportantYoungsters(param, data => {
          this.$popup.showNotification({
            type: "success",
            message: "重点青少年修改成功！"
          });
          this.resetForm();
          this.$parent.handleQuery();
        });
      }
    },
    /** 取消 */
    resetForm() {
      this.$refs["vBaseForm"].$refs["baseForm"].resetFields();
      this.$refs["dialogForm"].resetFields();
      this.$refs["vBaseForm"].baseForm = {};
      this.$data.dialogVisible = false;
      this.activeName = "first";
      this.isAdd = true;
      this.dialogForm = {
        iscriminal: 0,
        districtCode: ""
      };
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}

.add-dialog .el-select {
  width: 100%;
}

.add-dialog .el-cascader {
  width: 100%;
}
</style>