import request from '@/plugin/axios';
import publicService from "@/api/sys/service.js";

const service = {
    ...publicService,
    /**
     * 添加人口基本信息
     * 
     * @param {any} param 人口基本信息
     * @param {any} successFunc 回调函数
     */
    requestSavePeopleInfo(param, successFunc) {
        request({
            url: "/compre/hp/people/save",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 修改人口基本信息
     * 
     * @param {any} param 人口基本信息
     * @param {any} successFunc 回调函数
     */
    requestUpdatePeopleInfo(param, successFunc) {
        request({
            url: "/compre/hp/people/update",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 查询基本信息
     * 
     * @param {any} id 基本信息ID
     * @param {any} successFunc 回调函数
     */
    requestBasePeople(id, successFunc) {
        request({
            url: "/compre/hp/people/find?id=" + id,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 查询户信息
     * 
     * @param {any} successFunc 回调函数
     */
    requestHouse(successFunc) {
        request({
            url: "/compre/hp/house/findPage?page=1&pageSize=1000",
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 分页获取重点青少年列表
     * 
     * @param {any} param 分页参数
     * @param {any} successFunc 回调函数
     */
    requestPageImportantYoungsters(param, successFunc) {
        request({
            url: "/compre/hp/people/young/findPage?name=" + param.queryForm.name + "&districtCode=" + param.queryForm.districtCode + "&page=" + param.page + "&pageSize=" + param.pageSize,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 添加重点青少年
     * 
     * @param {any} param 重点青少年参数
     * @param {any} successFunc 回调函数
     */
    requestSaveImportantYoungsters(param, successFunc) {
        request({
            url: "/compre/hp/people/young/save",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 修改重点青少年
     * 
     * @param {any} param 重点青少年参数
     * @param {any} successFunc 回调函数
     */
    requestUpdateImportantYoungsters(param, successFunc) {
        request({
            url: "/compre/hp/people/young/update",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },

    /**
     * 删除重点青少年
     * 
     * @param {any} peopleId 重点青少年 ID
     * @param {any} successFunc 回调函数
     */
    requestDeleteImportantYoungsters(peopleId, successFunc) {
        request({
            url: "/compre/hp/people/young/delete?id=" + peopleId,
            method: "post",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 查询重点关注人信息
     * 
     * @param {any} param 查询参数  entity人员类型  focusId人员ID
     * @param {any} successFunc 回调函数
     */
    requestFocus(param, successFunc) {
        request({
            url: "/compre/"+ param.entity +"/find?id="+ param.focusId,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 查询基本人口的综治信息
     * 
     * @param {any} peoId 基本人口 ID
     * @param {any} successFunc 回调函数
     */
    requestFocusPeople(peoId, successFunc) {
        request({
            url: "/compre/hp/people/find/compreInfo?id="+ peoId,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 分页获取户籍人口列表
     * 
     * @param {any} param 分页参数
     * @param {any} successFunc 回调函数
     */
    requestPageHousePeople(param, successFunc) {
        request({
            url: "/compre/hp/people/house/findPage?name="+ param.name +"&houseId="+ param.houseId+ "&districtCode="+ param.districtCode +"&page="+ param.page +"&pageSize="+ param.pageSize,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 查询基本人口的综治信息
     * 
     * @param {any} peoId 基本人口 ID
     * @param {any} successFunc 回调函数
     */
    requestFocusPeople(peoId, successFunc) {
        request({
            url: "/compre/hp/people/find/compreInfo?id="+ peoId,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 查询重点关注人信息
     * 
     * @param {any} param 查询参数  entity人员类型  focusId人员ID
     * @param {any} successFunc 回调函数
     */
    requestFocus(param, successFunc) {
        request({
            url: "/compre/"+ param.entity +"/find?id="+ param.focusId,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
};

export default service;