<template>
  <el-dialog :custom-class="customClass" class="techo-details-wrapper" :title="title" :visible.sync="visible" :width="width"
    @open="handleOpenView" :fullscreen="width=='100%'" append-to-body>
    <div class="techo-details__body">
      <el-tabs class="techo-details-tabs" :value="current" type="card" @tab-click="handleClick">
        <el-tab-pane v-for="(tab, tabIndex) in layout.tabs" :key="tabIndex" :label="tab" :name="tab">
          <span slot="label"><i style="margin-right:10px;" class="el-icon-date"></i>{{tab}}</span>
          <div v-if="layout['tab'+tabIndex].type == 'content'" style="height:100%;">
            <el-row :gutter="10" type="flex" style="height: 55%;">
              <el-col :span="10" class="left-top">
                <el-row :gutter="10">
                  <el-col :span="6" style="text-align: center;">
                    <img src="./images/photo.jpg" style="height: 120px;width: 110px;">
                  </el-col>
                  <el-col :span="9">
                    <div class="tacho-people-item">
                      <span class="item-title">姓名：</span>
                      <span class="item-value">{{ data.name }}</span>
                    </div>
                    <div class="tacho-people-item">
                      <span class="item-title">出生日期：</span>
                      <span class="item-value">{{ data.birthDate && data.birthDate.split(" ")[0] }}</span>
                    </div>
                    <div class="tacho-people-item">
                      <span class="item-title">籍贯：</span>
                      <span class="item-value">{{ data.dicMap.nativePlace }}</span>
                    </div>
                    <div class="tacho-people-item">
                      <span class="item-title">学历：</span>
                      <span class="item-value">{{ data.dicMap.education }}</span>
                    </div>
                    <div class="tacho-people-item">
                      <span class="item-title">政治面貌：</span>
                      <span class="item-value">{{ data.dicMap.politicalStatus }}</span>
                    </div>
                  </el-col>
                  <el-col :span="9">
                    <div class="tacho-people-item">
                      <span class="item-title">身份证号：</span>
                      <span class="item-value">{{ data.idCardNo }}</span>
                    </div>
                    <div class="tacho-people-item">
                      <span class="item-title">性别：</span>
                      <span class="item-value">{{ data.dicMap.sex }}</span>
                    </div>
                    <div class="tacho-people-item">
                      <span class="item-title">民族：</span>
                      <span class="item-value">{{ data.dicMap.nation }}</span>
                    </div>
                    <div class="tacho-people-item">
                      <span class="item-title">婚姻状况：</span>
                      <span class="item-value">{{ data.dicMap.marriage }}</span>
                    </div>
                    <div class="tacho-people-item">
                      <span class="item-title">宗教信仰：</span>
                      <span class="item-value">{{ data.dicMap.religiousBelief }}</span>
                    </div>
                  </el-col>
                </el-row>
                <el-row :gutter="10">
                  <el-col :span="24">
                    <div class="techo-details-tabs-content row-border">
                      <el-row v-for="(row,rowIndx) in layout['tab'+tabIndex].tmpl" :key="rowIndx" type="flex" justify="space-around">
                        <template v-for="(item,itemIndex) in row">
                          <el-col class="column-title" :span="item.col[0]">{{item.title}}</el-col>
                          <el-col :span="item.col[1]">{{getData(item.field)}}</el-col>
                        </template>
                      </el-row>
                    </div>
                  </el-col>
                </el-row>
                <div class="other-info" @click="handleDetailsInfo">更多信息>></div>
              </el-col>
              <el-col :span="14" class="right-top">
                <el-row>
                  <!-- <el-col :span="1">
                    <div class="side-ball">重</div>
                  </el-col> -->
                  <el-col :span="24">
                    <div class="techo-details-tabs-content row-border">
                      <div class="side-ball">重</div>
                      <el-row v-for="(row,rowIndx) in details.imptantYoungOther['tab'+tabIndex].tmpl" :key="rowIndx"
                        type="flex" justify="space-around">
                        <template v-for="(item,itemIndex) in row">
                          <el-col class="column-title" :span="item.col[0]">{{item.title}}</el-col>
                          <el-col :span="item.col[1]">{{getData(item.field)}}</el-col>
                        </template>
                      </el-row>
                    </div>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
            <div class="bottom-family-table">
              <div class="title">家庭信息</div>
              <el-table :data="familyData" height="calc(100% - 44px)" border style="width: 100%">
                <el-table-column type="index" label="序号" width="50" align="center"></el-table-column>
                <el-table-column prop="name" label="姓名" width="180" align="center"></el-table-column>
                <el-table-column prop="dicMap.householdRelation" label="与户主关系" align="center"></el-table-column>
                <el-table-column prop="householdName" label="户主姓名" width="180" align="center"></el-table-column>
                <el-table-column prop="dicMap.sex" label="性别" align="center"></el-table-column>
                <el-table-column prop="phone" label="联系方式" align="center"></el-table-column>
                <el-table-column prop="dicMap.marriage" label="婚姻状况" align="center"></el-table-column>
                <el-table-column prop="idCardNo" label="身份证号" width="180" align="center"></el-table-column>
                <el-table-column label="操作" fixed="right" align="center" width="100">
                  <template slot-scope="scope">
                    <el-button size="mini" class="techo-list__body-table-button-view" @click="handleDetailsInfo(scope.row, 'housePeople')">查看</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
          <div v-if="layout['tab'+tabIndex].type == 'map'" class="techo-details-tabs-map">
            <vBaseMap ref="map"></vBaseMap>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'" :title="title"></techo-details>
  </el-dialog>
</template>

<script>
import vBaseMap from "@/map/base-map.vue";
import layout from "./layout.js";
import config from "./config/config.js";
import service from "../services/important_youngsters_service.js";
import detailsConfig from "@/components/techo-details/config/detail-config.js";
export default {
  components: {
    vBaseMap
  },
  props: {
    // 标题
    title: {
      type: String,
      default: "详情信息"
    },
    // 对话框宽度
    width: {
      type: String,
      default: "50%"
    },
    height: {
      type: String,
      default: "50%"
    },
    // 页面布局配置
    layout: {
      type: Object,
      default() {
        return layout;
      }
    }
  },
  data() {
    return {
      // 组件可见性
      visible: false,
      // 显示详情
      data: {
        dicMap: {
          sex: "",
          nation: "",
          marriage: "",
          politicalStatus: "",
          education: "",
          religiousBelief: "",
          occupationType: ""
        }
      },
      details: detailsConfig,
      focusData: [],
      current: layout.tabs[0],
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "housePeople",
      familyData: []
    };
  },
  computed: {
    // 返回自定义类，控制对话框高度
    customClass() {
      if (this.width == "100%") {
        return "";
      }
      if (this.height == "50%") {
        return "techo-details-height-50";
      } else if (this.height == "60%") {
        return "techo-details-height-60";
      } else if (this.height == "70%") {
        return "techo-details-height-70";
      } else if (this.height == "80%") {
        return "techo-details-height-80";
      } else if (this.height == "100%") {
        return "techo-details-height-100";
      }
    }
  },
  methods: {
    // 打开视图面板
    handleOpenView() {
      this.$nextTick(() => {});
      // 获取重点人信息
      this.getFocusPeople();
      // 获取家庭成员
      this.getFamilyPeople();
    },
    // 查询重点人信息
    getFocusPeople() {
      let that = this;
      let keyValue = _.cloneDeep(config.keyValue);
      service.requestFocusPeople(this.data.peopleId, data => {
        delete data.basePeopleId;
        delete data.housePeopleId;
        delete data.vitalYoungPeopleId;
        let arr = [];
        _.map(data, (item, index) => {
          if (item != null) {
            let obj = {
              name: keyValue[index].name,
              value: {
                focusId: item,
                entity: keyValue[index].entity,
                configKey: keyValue[index].configKey
              }
            };
            arr.push(obj);
          }
        });
        console.log(arr);
      });
    },
    // 查询家庭成员
    getFamilyPeople() {
      // 根据户ID 查询户籍人口 家庭人员信息
      service.requestPageHousePeople(
        {
          districtCode: "",
          name: "",
          houseId: this.data.houseId,
          page: "1",
          pageSize: "1000"
        },
        data => {
          this.familyData = data.list;
        }
      );
    },
    // 查看所有信息
    handleDetailsInfo(data, key) {
      let newData = key == undefined ? _.cloneDeep(this.data) : data;
      this.configKey = key || "housePeople";
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = newData;
        this.$refs["details"].visible = true;
      });
    },
    handleClick(tab, event) {
      // 单击到地图标签页时，适配地图容器大小
      if (this.layout["tab" + tab.index].type == "map") {
        this.$nextTick(function() {
          this.$refs["map"][0].mapApi.map.resize();
          this.handleInitMap(tab);
        });
      }
    },
    // 获取属性信息
    getData(key) {
      return _.get(this.data, key);
    },
    // 初始化地图
    handleInitMap(tab) {
      let map = this.layout["tab" + tab.index].display;
      this.$refs["map"][0].mapApi.clear();

      // TODO 后期需根据不同类型传入不同图片
      if (map.type == "location") {
        this.$refs["map"][0].mapApi.markerApi.addMarkers(
          [
            {
              type: "0101",
              lon: this.data[map.lonX],
              lat: this.data[map.latY]
            }
          ],
          {
            img_0101: require("./images/1.png")
          }
        );
        // 移动到标记点
        this.$refs["map"][0].mapApi.mapOper.setCenterAndZoom([
          this.data[map.lonX],
          this.data[map.latY]
        ]);
      }
    }
  }
};
</script>

<style lang="scss">
@import "./styles/techo-details.scss";
</style>